import { FormControl, Box, Stack, TextField, Button, Slider, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router";
import useFetch from "../../hooks/useFetch";
import usePost from "../../hooks/usePost";
import SymptomTags from "../UI/SymptomTags";

const INITIAL_DATA = {
  symptom: "",
  medical_term: "",
  tags: ""
};

const AddSymptom = () => {
  const { id } = useParams();
  const [data, setData] = useState(INITIAL_DATA);

  const { addNewSymptom, updateSymptom } = usePost();
  const { fetchSymptom } = useFetch();

  const history = useNavigate();

  async function handleSubmit(e) {
    e.preventDefault();
    if (id === "add") {
      await addNewSymptom(data);
      setData(INITIAL_DATA);
    } else {
      await updateSymptom(id, data);
      history(-1);
    }
  }

  useEffect(() => {
    if (id !== "add") {
      document.title = "Edit Symptom";
      async function fetchData() {
        const sx = await fetchSymptom(id);
        setData(sx);
      }
      fetchData();
    } else {
      document.title = "Add Symptom";
    }
  }, []);

  function updateFields(fields) {
    setData(prev => {
      return { ...prev, ...fields };
    });
  }

  return (
    <form onSubmit={handleSubmit}>
      <Stack spacing={2} sx={{ justifyContent: "center", alignItems: "center", mt: "20px" }}>
        <Box sx={{ width: "90%", margin: "0 auto" }}>
          <FormControl sx={{ width: "100%" }}>
            <TextField
              value={data.symptom}
              size="small"
              label="Symptom"
              id="symptom"
              onChange={e => updateFields({ symptom: e.target.value })}
            />
          </FormControl>
        </Box>

        <Box sx={{ width: "90%", margin: "0 auto" }}>
          <FormControl sx={{ width: "100%" }}>
            <TextField
              value={data.medical_term}
              size="small"
              label="Medical Term"
              id="medical-term"
              onChange={e => updateFields({ medical_term: e.target.value })}
            />
          </FormControl>
        </Box>

        <Box sx={{ width: "90%", margin: "0 auto" }}>
          <SymptomTags tags={data.tags} updateFields={updateFields} />
        </Box>

        <FormControl sx={{ margin: "30px auto" }}>
          <Button variant="contained" type="submit" sx={{ width: "200px" }}>
            {id === "add" ? "Add Symptom" : "Update Symptom"}
          </Button>
        </FormControl>
      </Stack>
    </form>
  );
};

export default AddSymptom;
