import { FormControl, Autocomplete, TextField } from "@mui/material";

const SymptomTags = ({ tags, updateFields, setTagValue, isNew = true }) => {
  const options = [
    "Constitutional/General",
    "Eyes",
    "Ear/Nose/Throat",
    "Cardiovascular",
    "Respiratory",
    "Gastrointestinal",
    "Genitourinary",
    "Gynecological",
    "Peripheral (extremities)",
    "Hematologic/Lymphatic",
    "Hormonal/Endocrinological",
    "Musculoskeletal",
    "Neurological",
    "Psychiatric",
    "Sexual history",
    "Past Medical History",
    "Skin"
  ];

  return (
    <FormControl sx={{ width: "100%" }}>
      <Autocomplete
        disablePortal
        autoHighlight
        size="small"
        id="symptom-tags"
        getOptionLabel={option => option}
        value={tags}
        options={options}
        renderInput={params => (
          <TextField
            {...params}
            label="Organ System (tags)"
            sx={{
              fontFamily: "Poppins",
              color: "#36454f",
              marginY: "5px",
              fontSize: "12px"
            }}
            value={tags}
          />
        )}
        onChange={(e, newValue) => {
          if (isNew) {
            updateFields({ tags: newValue });
          } else {
            setTagValue(newValue);
          }
        }}
        freeSolo
      />
    </FormControl>
  );
};

export default SymptomTags;
